@include body-class(true) {

	.sui-sidenav {

		&.fui-sidenav {

			ul.sui-vertical-tabs {
				margin-top: 0;

				li.sui-vertical-tab {
					padding: 0;

					a {
						padding: 5px 15px;
						border-radius: 20px;

						&.current {
							background-color: palette(silver, soft);
							color: palette(gray, dark);
						}
					}
				}
			}
		}

		.sui-mobile-nav {
			max-width: none;
		}
	}

	.sui-row-with-sidenav {

		.sui-sidenav {

			@media (max-width: 1199px) {
				margin-bottom: 20px;
			}
		}
	}
}

@media (min-width: 783px) {

	@include body-class() {

		> .sui-select {

			&.sui-select-dropdown-container--open {
				z-index: 100;
			}
		}
	}
}